* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country__name {
  font-size: 34px;
  font-weight: 700;
  margin-left: 10px;
  margin-bottom: 80px;
}
.list__item {
  display: flex;
  align-items: center;
}

.list__flag {
  margin-right: 20px;
}

.list__name {
  font-size: 19px;
  font-weight: 600;
}
